import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
import { ListGroup } from 'react-bootstrap';

class App extends Component {
  constructor(e) {
    super(e);

    // this.onVideoSelect = this.onVideoSelect.bind(this);
    function getParameterByName(name, url) {
			// console.log('window.location.href', window.location.href);
		    if (!url) url = window.location.href;
		    name = name.replace(/[\[\]]/g, "\\$&");
		    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
		        results = regex.exec(url);
		    if (!results) return null;
		    if (!results[2]) return '';
		    return decodeURIComponent(results[2].replace(/\+/g, " "));
		}

    this.fileType = getParameterByName('fileType');
    
  }

  // onVideoSelect(e, category, fileName) {
  //   console.log(category, fileName);
  //   const url = 'kp/panoWrapper.html?xml=kp/2k/videopano.xml&initvars.video=' + category + '/' + fileName;
  // }

  render() {
    const videoFiles = [
      {"category":"dinning", "files":["Anniesindoor", "Anniesoutdoor", "Blue-Frog", "Hanshe", "Limeindoor", "Limeoutdoor"]},
      {"category":"lifestyle", "files":["Fish", "Fruit", "Kids-Playing", "Meat", "Supermarket", "Vegetable"]},
      {"category":"office", "files":["Building-Look", "Lobby-View", "Lobby", "Me-Cafe", "Office", "Pantry", "Visa-Department"]},
      {"category":"school", "files":["Canteen-1", "Class-Room-7", "Computer-Class-8", "Music-Class-19", "Pe-Class-1", "School-Lobby-22"]},
      {"category":"transport", "files":["Didi-9", "Mobike", "Mtr", "Trafic-Jam"]}
    ]

    const groups = videoFiles.map((value, index) => {
      // console.log( value, index)
      const category = value.category;
      const files = value.files;

      return (
        <div key={value + index}>
          <h3>{category}</h3>
          <ListGroup>
            {files.map((v, idx)=>
                <ListGroup.Item action key={v + idx} 
                href={'kp/panoWrapper.html?xml=./2k/videopano.xml&initvars.file=' + category + '/' + v + '&initvars.fileType=' + this.fileType}
                // onClick={(e)=>this.onVideoSelect(e,category, v)}
                >
                  {v}
                </ListGroup.Item>
            )}
          </ListGroup>
          <hr/>
        </div>
      );
    });

    return (
      <div className="App">
      {groups}
        {/* <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.js</code> and save to reload.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
          </a>
        </header> */}
      </div>
    );
  }
}

export default App;
